import React, { memo, useRef, useState } from "react";
import styled from "styled-components";
import Pagination from "../../common/components/Pagination";
import { useAgencyCodes } from "../../common/hooks/useAgencyCodes";
import useCreateCode from "../../common/hooks/useCreateCode";
import ListCodeTable from "./ListCodeTable";
import HashLoader from "react-spinners/HashLoader";

const AgencyDashBoard = ({ className }) => {
  const [page, setPage] = useState(1);
  const { mutate: createCode, data: newCode, isLoading } = useCreateCode();
  const [codesData] = useAgencyCodes(page);
  const { none_use, used, codes } = codesData || {};
  const { last_page, data } = codes || {};
  const table = useRef(null);

  return (
    <main className={className}>
      <section className={`container mt-3`}>
        <header className="fs__title--main fw-bolder mb-3">Quản lý báo cáo</header>
        <div className="d-flex flex-column flex-md-row">
          <div className="d-flex col-12 col-md-6">
            <div className="management__card w-50">
              <p className="mb-0">Chưa sử dụng</p>
              <p className="fs__title--main">{none_use || 0}</p>
            </div>
            <div className="management__card w-50">
              <p className="mb-0">Đã sử dụng</p>
              <p className="fs__title--main">{used || 0}</p>
            </div>
          </div>

          <div className="management__form">
            <p className="fs__title">Tạo mã mới</p>
            <form className="d-flex">
              <input
                className="management__form__input"
                type="text"
                name=""
                id=""
                value={newCode?.code}
                readOnly
              />
              <button
                className="management__form__button"
                type="button"
                disabled={isLoading}
                onClick={createCode}
              >
                {!isLoading ? (
                  <div>Tạo mã mới</div>
                ) : (
                  <HashLoader color="#fafafa" size={24} loading={isLoading} />
                )}
              </button>
            </form>
            <p className="fs__smallest mt-4">Chú ý:...</p>
          </div>
        </div>
        <p className="fs__title fw-bolder mb-3 mt-5">Danh sách khách hàng</p>
      </section>
      <ListCodeTable codes={data} ref={table} />
      {codesData && (
        <Pagination pageCount={last_page} initialPage={page} onPageChange={setPage} list={table} />
      )}
    </main>
  );
};

export default memo(styled(AgencyDashBoard)`
  .management {
    &__form {
      padding: 10px 0 10px 50px;
      margin-left: 30px;
      border-left: 1px solid ${(props) => props.theme.colorSecondary};
      @media ${(props) => props.theme.breakPoints.md} {
        margin-left: 0;
      }
      &__input {
        color: ${(props) => props.theme.colorSecondary};
        padding: 10px 0;
        text-indent: 10px;
        border: 0;
        background-color: ${(props) => props.theme.colorCard};
        box-shadow: 0 0 0 2px ${(props) => props.theme.colorSecondary};

        &:focus {
          outline: none;
        }
      }
      &__button {
        min-width: 112px;
        color: #fafafa;
        background-color: ${(props) => props.theme.colorSecondary};
        padding: 10px;
        border: 0;
        font-weight: bolder;
        /* &:hover {
          background-color: ${(props) => props.theme.colorSecondary};
          color: #000;
        } */
      }
    }
    &__card {
      width: 200px;
      padding: 20px;
      border-radius: 16px;
      background-color: ${(props) => props.theme.colorCard};
      margin: 20px 20px 20px 0;
      p:nth-child(2) {
        font-weight: bolder;
      }
    }
  }
`);
