import { useMutation, useQueryClient } from "react-query";
import useAxios from "./useAxios";
import { config } from "../../config/api";
import useHandleErrors from "./useHandleErrors";

const useCreateCode = () => {
  const axios = useAxios();
  const queryClient = useQueryClient();
  const [handleError] = useHandleErrors();
  const query = useMutation(
    async () => {
      const { data } = await axios.post(`${config.apiGateWay}/dashboard/parter`);
      return data.code;
    },
    {
      // onSuccess: (data) => {
      //   // console.log(data);
      //   queryClient.setQueryData(["agencyCodes"], (prev) => {
      //     return { ...prev, data: [data, ...prev.data] };
      //   });
      // },
      onError: (err) => {
        handleError(err);
      },
      onSettled: () => {
        queryClient.invalidateQueries(["user", "agencyCodes"]);
      },
    }
  );
  return query;
};

export default useCreateCode;
