import React, { forwardRef, memo, useRef } from "react";
import styled from "styled-components";

const ListCodeTable = ({ className, codes }, ref) => {
  const tempInput = useRef(null);

  const copyToClipBoard = (data) => {
    if (!tempInput) return;
    tempInput.current.value = data;
    tempInput.current.select();
    document.execCommand("copy");
  };
  return (
    <main className={`${className} management__table container-fluid`} ref={ref}>
      <div className="container overflow-auto">
        <table>
          <tbody>
            <tr className="d-none d-lg-table-row">
              <th>#</th>
              <th>Code</th>
              <th>Họ và tên</th>
              <th>Tình trạng</th>
              <th>Link kích hoạt</th>
            </tr>

            {codes?.map(({ id_c, code, fullname, id }) => (
              <tr key={id_c}>
                <td>{id_c}</td>
                <td>
                  <button
                    className="management__table--icon"
                    onClick={() => {
                      copyToClipBoard(code);
                    }}
                  >
                    {code}&nbsp;
                    <i className="far fa-clipboard"></i>
                  </button>
                </td>
                <td>{fullname}</td>
                <td className="fw-bolder" style={{ color: !id ? "#2fb32f" : "#f3390f" }}>
                  {!id ? "Chưa sử dụng" : "Đã sử dụng"}
                </td>
                <td>
                  <div>
                    <input
                      readOnly
                      type="text"
                      value={!id ? `${window.location.hostname}/code/?ref=${code}` : ""}
                    />
                    {!id && (
                      <input
                        className="management__table--button"
                        type="button"
                        value="Sao chép"
                        onClick={() => {
                          copyToClipBoard(
                            !id ? `${window.location.hostname}/code/?ref=${code}` : ""
                          );
                        }}
                      />
                    )}
                  </div>
                </td>
              </tr>
            ))}
            {/* <td>1</td>
              <td>
                <button
                  className="management__table--icon"
                  data-share="342T3F"
                  onClick={() => {
                    copyToClipBoard("342T3F");
                  }}
                >
                  342T3F
                  <i className="far fa-clipboard"></i>
                </button>
              </td>
              <td>Võ Quyết Thắng</td>
              <td>Đã dùng</td> */}
          </tbody>
        </table>
        <input type="text" id="temp" readOnly ref={tempInput} />
      </div>
    </main>
  );
};

export default memo(styled(forwardRef(ListCodeTable))`
  position: relative;
  &::before {
    margin-top: 30px;
    z-index: -1;
    content: "";
    background-color: #225d5a;
    position: absolute;
    top: 0;
    height: 44px;
    width: 100%;
  }
  padding: 30px 0;
  /* background-color: ${(props) => props.theme.colorCard}; */
  background-color: #2b2b2b0d;
  table {
    width: 100%;
    tr {
      &:first-of-type {
        background-color: #225d5a;
        color: #fafafa;
      }
    }
  }

  th {
    padding: 10px 30px 10px 0;
  }
  td {
    /* $py: 20px; */
    padding: 20px 30px 20px 0;
    input {
      color: ${(props) => props.theme.colorSecondary};
      border: 0;
      outline: none;
      background-color: rgba(255, 255, 255, 0);
    }
    input[type="button"] {
      color: black;
      background-color: ${(props) => props.theme.colorCard};
      padding: 10px;
      font-weight: bolder;
      border: 1.5px solid ${(props) => props.theme.colorSecondary};
      transition: all 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);
      &:hover {
        background-color: ${(props) => props.theme.colorSecondary};
        color: #fafafa;
      }
    }
  }
  #temp {
    position: absolute;
    opacity: 0;
  }
  .modal__custom {
    background-color: #2b2b2bfa;
    &__header,
    &__footer {
      border: 0;
    }
    &__button--close {
      color: #fafafa !important;
      font-weight: bolder;
      &:hover {
        color: ${(props) => props.theme.colorTextTitle};
      }
    }
  }
  .management {
    &__table {
      &--icon {
        white-space: nowrap;
        background-color: rgba(255, 255, 255, 0);
        border: 0;
        color: ${(props) => props.theme.colorTextTitle};
      }
    }
  }
  @media ${(props) => props.theme.breakPoints.lg} {
    overflow-x: auto;
    td {
      position: relative;
      display: flex;
      &::before {
        /* Now like a table header */
        /* Top/left values mimic padding */

        width: 20%;

        white-space: nowrap;
        padding-left: 8px;
      }

      &:nth-of-type(1) {
        background-color: #225d5a;
        color: #fafafa;
        font-weight: bolder;
        display: flex;
        justify-content: space-between;
        &::before {
          content: "#";
        }
      }
      &:nth-of-type(2):before {
        content: "Code";
      }
      &:nth-of-type(3):before {
        content: "Name";
      }
      &:nth-of-type(4):before {
        content: "Active";
      }
      &:nth-of-type(5):before {
        content: "Link";
      }
    }
  }
`);
